import React, { useState, useEffect } from 'react';
import { Drawer, InputBase, ThemeProvider, Typography } from '@mui/material';
import SearchIcon from '@mui/icons-material/Search';
import TuneIcon from '@mui/icons-material/Tune';

import CloseIcon from '@mui/icons-material/Close';

import { useCreateMemoTheme } from '../../theme';
import { useDarkMode } from 'storybook-dark-mode';
import { useTranslation } from 'react-i18next';
import _ from 'lodash';
import {
	FilterProps,
	AnchorEnum,
	FilterTypeEnum,
	FilterQuery,
	SelectFilter,
	FilterTypes
} from './filters.types';
import { SearchBarChips } from './SearchBarChips';
import { DrawerFiltersRender } from './DrawerFiltersRender';
import { makeStyles } from '@mui/styles';

export function getObjKeysTrue(obj: Object) {
	if (!obj) return [];
	return Object.keys(obj).filter((k) => obj[k] === true);
}

export function getSelectLabelByFilterKey(
	filterKey: SelectFilter['filterKey'],
	filters: FilterTypes[],
	filterState
) {
	const setValue = getObjKeysTrue(filterState[filterKey])?.[0];
	// find filter by filterKey
	// @ts-ignore
	const filter = filters.find((f) => f.filterKey === filterKey) as SelectFilter;
	return filter?.items?.find((item) => item.value === setValue)?.label;
}

export const Filter = (props: FilterProps) => {
	const { t } = useTranslation();
	const mode = useDarkMode() ? 'dark' : 'light';
	const { palette } = useCreateMemoTheme(mode);
	const designSystemTheme = useCreateMemoTheme();
	const {
		side,
		trigger,
		textSearch,
		filters,
		onChange: onChangeEmitter,
		drawerHeadline,
		updateFilters
	} = props;
	const [toggleDrawerState, setToggleDrawerState] = useState<null | AnchorEnum>(null);
	const useStyles = makeStyles((theme) => ({
		searchBarContainer: {
			backgroundColor: theme.palette.grey[200],
			borderRadius: '20px',
			width: `calc(100% - ${theme.spacing(2)})`,
			margin: `${theme.spacing(2)} ${theme.spacing(1)}`,
			boxSizing: 'border-box',
			padding: `0 ${theme.spacing(2)}`
		},
		searchIconContainer: {
			display: 'flex',
			alignItems: 'center',
			justifyContent: 'center',
			width: theme.spacing(3),
			height: theme.spacing(3),
			marginRight: theme.spacing(1)
		},
		searchBarInputContainer: { width: `calc(100% - ${theme.spacing(4)})` },
		tuneIconContainer: {
			display: 'flex',
			alignItems: 'center',
			justifyContent: 'center',
			width: theme.spacing(3),
			height: theme.spacing(3),
			marginRight: theme.spacing(2),
			color: palette.grey[600]
		},
		closeIcon: {
			position: 'absolute',
			m: 2,
			right: theme.spacing(1.5),
			padding: theme.spacing(1.5),
			height: theme.spacing(6),
			width: theme.spacing(6),
			top: theme.spacing(0.5),
			cursor: 'pointer'
		}
	}));
	const classes = useStyles(props);

	const [filterState, _setFilterState] = useState<Partial<FilterQuery>>({});
	const [compMounted, setCompMounted] = useState(false);
	// this is used to not trigger the onChangeEmitter on initial render
	const setFilterState = (newState: Partial<FilterQuery>, triggerEmitter = true) => {
		const tmp = _.clone(newState);
		_setFilterState(newState);
		if (triggerEmitter) {
			const stateToEmit = formatFilterQuery(tmp);
			onChangeEmitter(stateToEmit);
		}
	};
	const [textSearchState, setTextSearchState] = useState<string>('');

	useEffect(() => {
		if (!compMounted) createInitialFilterState();
	}, [filters]);

	useEffect(() => {
		createInitialFilterState();
	}, [updateFilters]);

	function createInitialFilterState() {
		const initialFilterState = {};

		for (const filterItem of filters) {
			if (filterItem.filterType === FilterTypeEnum.SECTION_HEADER) {
				continue;
			}
			if (filterItem.defaultValue) {
				initialFilterState[filterItem.filterKey] = {
					[filterItem.defaultValue]: true
				};
			} else {
				initialFilterState[filterItem.filterKey] = {};
			}
		}
		initialFilterState[textSearch.filterKey] = { value: textSearch.defaultValue || '' };
		setTextSearchState(textSearch.defaultValue || '');
		setCompMounted(true);
		setFilterState(initialFilterState, false);
	}

	function formatFilterQuery(_filterState): FilterQuery {
		// @ts-ignore
		const stateToEmit: FilterQuery = {};
		for (const [key, value] of Object.entries(_filterState)) {
			// @ts-ignore
			const filter = filters.find((predicate) => predicate?.filterKey === key);
			if (filter?.filterType === FilterTypeEnum.CHIP) {
				stateToEmit[key] = getObjKeysTrue(value);
			} else {
				stateToEmit[key] = getObjKeysTrue(value)?.[0];
			}
		}
		if (_filterState[textSearch.filterKey]) {
			stateToEmit[textSearch.filterKey] = _filterState[textSearch.filterKey].value;
		}
		return stateToEmit;
	}
	const updateTextSearch = (value: string) => {
		setTextSearchState(value);
		setFilterState({ ...filterState, [textSearch.filterKey]: { value } });
	};

	const removeSelectedFilter = (filterKey: string) => {
		const newFilterState = { ...filterState, [filterKey]: {} };
		setFilterState(newFilterState);
	};

	const updateFilterState = (filterKey: string, value: string | number) => {
		let newValue: any;

		// @ts-ignore
		const filter = filters.find((predicate) => predicate.filterKey === filterKey);
		// find chip items that are exclusive
		const exclusiveFilters =
			// @ts-ignore
			filter?.items?.filter((item) => item.exclusive).map((item) => item.value) || [];
		const filterIsExclusive = exclusiveFilters.includes(value);
		// the first condition here is for filters that allow multiple selections
		if (filter.filterType === FilterTypeEnum.CHIP && !filterIsExclusive) {
			newValue = {
				...filterState[filterKey],
				[value]: filterState[filterKey][value] ? false : true
			};
			exclusiveFilters.forEach((item) => {
				newValue[item] = false;
			});
		} else {
			if (!value) {
				removeSelectedFilter(filterKey);
				return;
			} else {
				newValue = {
					[value]: filterState?.[filterKey]?.[value] ? false : true
				};
			}
		}

		const newFilterState = { ...filterState, [filterKey]: newValue };
		setFilterState(newFilterState);
	};

	const toggleDrawer = (anchor: AnchorEnum) => (event: React.KeyboardEvent | React.MouseEvent) => {
		if (
			event.type === 'keydown' &&
			((event as React.KeyboardEvent).key === 'Tab' ||
				(event as React.KeyboardEvent).key === 'Shift')
		) {
			return;
		}
		setToggleDrawerState(anchor);
	};

	return (
		<div>
			<ThemeProvider theme={designSystemTheme}>
				<React.Fragment key={side}>
					{trigger ? (
						<span onClick={toggleDrawer(side)}>{trigger}</span>
					) : (
						// RENDER SEARCHBAR
						textSearch && (
							<div>
								<div className={classes.searchBarContainer}>
									<div
										style={{
											display: 'flex',
											alignItems: 'center',
											width: '100%'
										}}
									>
										<span>
											<div className={classes.searchIconContainer}>
												<SearchIcon />
											</div>
										</span>
										<span className={classes.searchBarInputContainer}>
											<InputBase
												id="order_filters_field_general_search"
												onChange={(e) => updateTextSearch(e.currentTarget.value)}
												placeholder={textSearch.placeholder || t('general:search')}
												value={textSearchState}
												sx={{ width: '100%' }}
												inputProps={{ 'aria-label': 'Search' }}
											/>
										</span>
										<span
											onClick={toggleDrawer(side)}
											style={{
												cursor: 'pointer'
											}}
										>
											<div className={classes.tuneIconContainer}>
												<TuneIcon />
											</div>
										</span>
									</div>
								</div>
								<SearchBarChips
									filters={filters}
									filterState={filterState}
									removeSelectedFilter={removeSelectedFilter}
									updateFilterState={updateFilterState}
								/>
							</div>
						)
					)}
					<Drawer
						anchor={side}
						open={Object.values(AnchorEnum).includes(toggleDrawerState) ? true : false}
						onClose={toggleDrawer(null)}
						style={{ zIndex: 1300 }}
						PaperProps={{
							sx: { width: '320px' }
						}}
					>
						<CloseIcon onClick={toggleDrawer(null)} className={classes.closeIcon} />
						{drawerHeadline && (
							<Typography variant="h5" sx={{ m: 2 }}>
								{drawerHeadline}
							</Typography>
						)}
						<DrawerFiltersRender
							filters={filters}
							filterState={filterState}
							updateFilterState={updateFilterState}
						/>
					</Drawer>
				</React.Fragment>
			</ThemeProvider>
		</div>
	);
};
