import { Chip } from '@mui/material';
import React from 'react';
import { FilterTypeEnum } from './filters.types';
import { getObjKeysTrue, getSelectLabelByFilterKey } from './index';
import CancelIcon from '@mui/icons-material/Cancel';
import CheckIcon from '@mui/icons-material/Check';
import { makeStyles } from '@mui/styles';

export function SearchBarChips(props) {
	const { filters, filterState, removeSelectedFilter, updateFilterState } = props;
	const getSelectedLabel = (filterKey: string) =>
		getSelectLabelByFilterKey(filterKey, filters, filterState);
	const useStyles = makeStyles((theme) => ({
		chipMargin: {
			margin: `${theme.spacing(1)} ${theme.spacing(1)}`
		}
	}));
	const classes = useStyles(props);
	return (
		<div>
			{Array.isArray(filters) &&
				[...filters] // prevent mutation of original array
					.sort((m1, m2) => (m1.filterType === 'chip' ? -1 : 1))
					.map((filterItem) => {
						switch (filterItem.filterType) {
							case FilterTypeEnum.CHIP:
								return (
									<span key={filterItem.filterKey}>
										{filterItem.items.map((item) => {
											return (
												<Chip
													key={item.value}
													onClick={() => {
														updateFilterState(filterItem.filterKey, item.value);
													}}
													variant={
														filterState[filterItem.filterKey]?.[item.value] ? 'filled' : 'outlined'
													}
													size="small"
													color={'primary'}
													label={`${item.label}`}
													icon={
														filterState[filterItem.filterKey]?.[item.value] ? <CheckIcon /> : null
													}
													className={classes.chipMargin}
												/>
											);
										})}
									</span>
								);
							case FilterTypeEnum.SELECT:
								return (
									getObjKeysTrue(filterState[filterItem.filterKey]).length > 0 && (
										<Chip
											key={filterItem.filterKey}
											variant={'filled'}
											size="small"
											color={'primary'}
											onDelete={() => removeSelectedFilter(filterItem.filterKey)}
											label={`${getSelectedLabel(filterItem.filterKey)}`}
											deleteIcon={<CancelIcon />}
											className={classes.chipMargin}
										/>
									)
								);
							default:
								return null;
						}
					})}
		</div>
	);
}
