import { Box, List, ListItem, ListItemText, Select, MenuItem, Chip } from '@mui/material';
import React from 'react';
import { getSelectLabelByFilterKey, getObjKeysTrue } from '.';
import { FilterTypeEnum } from './filters.types';
import CheckIcon from '@mui/icons-material/Check';
import { makeStyles } from '@mui/styles';

export function DrawerFiltersRender(props) {
	const { filters, filterState, updateFilterState } = props;
	const useStyles = makeStyles((theme) => ({
		paddingBottom0: {
			paddingBottom: theme.spacing(0)
		},
		paddingTop0: {
			paddingTop: theme.spacing(0)
		},
		width100: {
			width: '100%'
		},
		chipContainer: {
			margin: `${theme.spacing(0.5)} ${theme.spacing(1)}`
		},
		chipMargin: {
			margin: `${theme.spacing(0.5)} ${theme.spacing(0.5)}`
		}
	}));
	const classes = useStyles(props);
	const getSelectedLabel = (filterKey: string) =>
		getSelectLabelByFilterKey(filterKey, filters, filterState);
	return (
		<>
			{Array.isArray(filters) &&
				filters.map((filterItem) => {
					switch (filterItem.filterType) {
						case FilterTypeEnum.SECTION_HEADER:
							return (
								<Box role="presentation" key={filterItem.label}>
									<List className={classes.paddingBottom0}>
										<ListItem>
											<ListItemText primary={filterItem.label} />
										</ListItem>
									</List>
								</Box>
							);
						case FilterTypeEnum.SELECT:
							return (
								<Box role="presentation" key={filterItem.filterKey}>
									<List className={classes.paddingTop0}>
										<ListItem>
											<Select
												renderValue={() =>
													getSelectedLabel(filterItem.filterKey) || filterItem.placeholder
												}
												size="medium"
												displayEmpty={true}
												onChange={(e) => updateFilterState(filterItem.filterKey, e.target.value)}
												value={getObjKeysTrue(filterState[filterItem.filterKey])?.[0] || ''}
												className={classes.width100}
											>
												{filterItem.resetItem && (
													<MenuItem
														key={filterItem.resetItem.label}
														value={filterItem.resetItem.value}
													>
														{filterItem.resetItem.label}
													</MenuItem>
												)}
												{filterItem?.items?.map((option) => (
													<MenuItem key={option.label} value={option.value || ''}>
														{option.label}
													</MenuItem>
												))}
											</Select>
										</ListItem>
									</List>
								</Box>
							);
						case FilterTypeEnum.CHIP:
							return (
								<span className={classes.chipContainer} key={filterItem.filterKey}>
									{filterItem.items.map((item) => {
										return (
											<Chip
												key={item.value}
												onClick={() => {
													updateFilterState(filterItem.filterKey, item.value);
												}}
												variant={
													filterState[filterItem.filterKey]?.[item.value] ? 'filled' : 'outlined'
												}
												size="small"
												color={'primary'}
												label={`${item.label}`}
												icon={
													filterState[filterItem.filterKey]?.[item.value] ? <CheckIcon /> : null
												}
												className={classes.chipMargin}
											/>
										);
									})}
								</span>
							);
						default:
							return null;
					}
				})}
		</>
	);
}
